  display: none;
  width: 100%;
  height: 100%;
  background-color: rgb(211, 241, 245);
  position: absolute;
  top: 0;
  left: 0;
}

.win-open .winners {
  display: block;
}

.winners__list-item {
  display: flex;
  gap: 22px;
}

.winners__list-item div:nth-child(1) {
  width: 15px;
}

.winners__list-item div:nth-child(2) {
  width: 50px;
}

.winners__list-item div:nth-child(3) {
  width: 90px;
}

.winners__list-item div:nth-child(4) {
  width: 30px;
}

.winners__list-item div:nth-child(5) {
  width: 30px;
}

.winners__list-header {
  display: flex;
  gap: 22px;
}

.winners__list-header div:nth-child(1) {
  width: 15px;
}

.winners__list-header div:nth-child(2) {
  width: 50px;
}

.winners__list-header div:nth-child(3) {
  width: 90px;
}

.winners__list-header div:nth-child(4) {
  cursor: pointer;
  width: 30px;
}

.winners__list-header div:nth-child(5) {
  cursor: pointer;
  width: 30px;
}

.pop-up {
  width: 300px;
  height: 114px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  background-color: rgb(222, 244, 247);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  /* display: none; */
  opacity: 0;
  transition: 1.5s;

  top: -250%;
}

.pop-up_show {
  opacity: 1;
  top: 50%;
  transition: 1.5s;
