export const data = {
    winners: {
        page: 1,
        sortType: 'time',
        winsOrderType: 'ASC',
        timeOrderType: 'ASC',
        isWin: false,
        winners: [],
        getPage() {
            return this.page;
        },
        nextPage() {
            this.page += 1;
        },
        prevPage() {
            this.page -= 1;
        },
        getIsWin() {
            return this.isWin;
        },
        setIsWin(isWin) {
            this.isWin = isWin;
        },
        existWinnerCheck(id, time) {
            const racerData = Object.assign({ id }, this.countWinsData(id, time));
            if (!this.isWin && racerData.wins === 1) {
                this.winners.push(racerData);
                return Object.assign({ firstWin: true }, racerData);
            }
            if (!this.isWin && racerData.wins > 1) {
                return Object.assign({ firstWin: false }, racerData);
            }
            return undefined;
        },
        countWinsData(id, time) {
            const curRacer = this.winners.find((e) => e.id === id);
            if (curRacer) {
                curRacer.wins += 1;
                curRacer.time = curRacer.time < time ? curRacer.time : time;
                return { wins: curRacer.wins, time: curRacer.time };
            }
            return { wins: 1, time };
        },
    },
    garage: {
        pageNumber: 1,
        stoppedRacers: 1,
        isRace: false,
        getPageNumber() {
            return this.pageNumber;
        },
        nextPageNumber() {
            this.pageNumber += 1;
        },
        prevPageNumber() {
            this.pageNumber -= 1;
        },
        getIsRace() {
            return this.isRace;
        },
        setIsRace(isRace) {
            this.isRace = isRace;
        },
    },
    form: {
        selectedId: 0,
        setSelectedId(id) {
            this.selectedId = id;
            const selectBtn = (document.querySelector('button[data-type="btn-update"]'));
            selectBtn.disabled = false;
        },
    },
};
